export default {
  name: "Home",
  async asyncData({ $axios }) {
    // Popular Tags
    try {
      let { tags } = await $axios.$get("tags");
      return { tags };
    } catch {
      let tags = [];
      return { tags };
    }
  },
  data() {
    return {
      // 是否已登录
      isSignedIn: false,
      //
      currentFeed: "Global",
      //
      articles: [],
      articlesCount: 0,
      //
      pageLimit: 10,
      pageIndex: 1,
      //
      currentTag: null,
      //
      feedLoading: false,
    };
  },
  watch: {
    // $route: {
    //   // 立即执行
    //   immediate: true,
    //   // 回调
    //   handler() {
    //     console.log(1);
    //   },
    //   // end
    // },
  },
  created() {
    this.isSignedIn = !!(process.client && localStorage.getItem("jwtToken"));
    this.isSignedIn
      ? this.handleChangeFeed("Your")
      : this.handleChangeFeed("Global");
  },
  methods: {
    // 切换 tab
    handleChangeFeed(feed) {
      this.currentTag = null;
      this.currentFeed = feed;
      this.handleGetFeed();
    },
    // 获取文章流
    handleGetFeed() {
      this.feedLoading = true;
      this.articles = [];
      this.articlesCount = 0;
      // 请求参数
      let params = {
        limit: this.pageLimit,
        offset: (this.pageIndex - 1) * this.pageLimit,
        tag: this.currentTag,
        author: null,
        favorited: null,
      };
      // 请求对象
      let request = undefined;
      //
      if (this.currentFeed === "Global" || this.currentFeed === "Tag")
        request = this.$axios.$get("articles", { params });
      if (this.currentFeed === "Your")
        request = this.$axios.$get("articles/feed", { params });

      request
        .then(({ articles, articlesCount }) => {
          this.articles = articles;
          this.articlesCount = articlesCount;
        })
        .finally(() => {
          this.feedLoading = false;
        });
    },
    // 选择页码
    handleCheckPage(index) {
      this.pageIndex = index;
      this.handleGetFeed();
    },
    // 选择标签
    handleCheckTag(tag) {
      this.currentFeed = "Tag";
      this.currentTag = tag;

      this.handleGetFeed();
    },
  },
};
